___CSS_LOADER_EXPORT___.push([module.id, `:root {
  --cor-fundo: rgb(241, 135, 153);
  --cor-fundo-clara: rgb(243, 175, 187);
  --cor-fundo-dark: rgb(236, 86, 111);
}

* {
  box-sizing: border-box;
  outline: none;
}

body {
  margin: 0;
  padding: 0;
  background: var(--cor-fundo);
  font-family: 'Poppins', 'serif';
  font-size: 1.3em;
  line-height: 1.5em;
}

.container {
  max-width: 640px;
  margin: 50px auto;
  background: #fff;
  padding: 20px;
  border-radius: 10px;
}

form input, form label, form button {
  display: block;
  width: 100%;
  margin-bottom: 10px;
  border-radius: 8px;
}

form input {
  font-size: 24px;
  height: 50px;
  padding: 0 20px;
}

form input:focus {
  outline: 2px solid var(--cor-fundo);
}

form button {
  border: none;
  background: var(--cor-fundo);
  color: #fff;
  font-size: 18px;
  font-weight: bold;
  height: 50px;
  cursor: pointer;
  margin-top: 30px;
}

form button:hover {
  background: var(--cor-fundo-dark);
}

.container h1 {
  text-align: center;
  margin-bottom: 40px;
}

.cpf-gerado {
  font-size: 2em;
  color: var(--cor-fundo);
  text-align: center;
  font-weight: bold;
  margin-bottom: 40px;
  letter-spacing: 2px;
}
